// @import-normalize;
@import "helper.scss";

* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

*::before {
  box-sizing: border-box;
}
*::after {
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
}

button {
  font-family: inherit;
}
:focus {
  outline: none;
}

.clearfix::after {
  content: "";
  display: block;
  clear: both;
}

.oneLine {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.icon {
  width: 16px;
  height: 16px;
}
